*, *:before, *:after {
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  min-height: 100%;
}

.content {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Koulen", cursive;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #6b7280;
  line-height: 1.8;
}

.tagline {
  font-family: "Koulen", cursive;
  font-weight: 900;
  color: #000;
  font-size: 3rem;
  text-align: center;
}

.descripcion-pagina {
  text-align: center;
}

a {
  text-decoration: none;
  color: black;
}

/** Media Queries **/
.about-me {
  margin-top: 2rem;
}
.about-me h2 {
  text-align: center;
  margin-bottom: 1rem;
}
.about-me .about-me__content {
  font-family: "Dosis", sans-serif;
  margin: 0 auto;
  text-align: center;
}
@media (min-width: 768px) {
  .about-me .about-me__content {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    text-align: left;
  }
}

@media (min-width: 768px) {
  .info_me {
    width: 55rem;
    height: 30rem;
    margin-left: 1rem;
  }
}
.info_me .bar {
  display: flex;
  justify-content: center;
  margin-bottom: 1rem;
}
.info_me .bar .circle {
  margin-right: 1rem;
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  border: 3px solid black;
}
.info_me .bar .circle.selected {
  background-color: var(--gray);
  transition: all 0.5s ease;
}

.social {
  margin-top: 1rem;
}
.social .social_ {
  display: flex;
  justify-content: center;
  margin: 2rem 0;
}
.social .social_ div {
  margin-right: 1rem;
}
.social .social_ div i {
  color: black;
  font-size: 3.5rem;
  text-align: center;
}

.custom-shape-divider-top-1691861675 {
  top: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  line-height: 0;
}

.custom-shape-divider-top-1691861675 svg {
  position: relative;
  display: block;
  width: 100%;
  height: 50px;
}
@media (min-width: 768px) {
  .custom-shape-divider-top-1691861675 svg {
    width: calc(100% + 1.3px);
    height: 150px;
  }
}

.custom-shape-divider-top-1691861675 .shape-fill {
  fill: #FFFFFF;
}

header.header, footer.footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  background-color: black;
  padding: 1.5rem 0.5rem;
  border-bottom: 8px solid #460b0b;
}
header.header a, footer.footer a {
  color: white;
  font-size: 2rem;
  margin: 0;
  font-weight: bold;
}

.navegacion {
  margin-right: 1.5rem;
}
.navegacion a {
  font-size: 1.8rem;
  margin-right: 1rem;
}

main {
  width: 100%;
}
main #banner {
  display: flex;
  width: 100%;
  height: 33rem;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  line-height: 1;
  font-weight: bold;
}
main #banner span:first-child {
  font-size: 6.5rem;
}
@media (min-width: 768px) {
  main #banner span:first-child {
    font-size: 10rem;
  }
}
main #banner span:nth-child(2) {
  background-color: black;
  width: 35rem;
  font-size: 4rem;
}
@media (min-width: 768px) {
  main #banner span:nth-child(2) {
    width: 50rem;
    font-size: 8rem;
  }
}
main #banner span:nth-child(3) {
  font-size: 2rem;
  margin: 1rem 0;
}
@media (min-width: 768px) {
  main #banner span:nth-child(3) {
    font-size: 3.5rem;
  }
}
main #banner #reload {
  width: 3rem;
  text-align: center;
  margin: 0 auto;
}
main #banner #reload:hover {
  cursor: pointer;
}

.projects {
  margin-top: 2rem;
  padding: 1.5rem;
  background-color: var(--gray);
  background-color: #f0f0f0;
}
.projects h2 {
  text-align: center;
}
.projects .projects-cards {
  display: grid;
  justify-content: center;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .projects .projects-cards {
    grid-template-columns: repeat(3, 1fr);
    gap: 1.6rem;
  }
}

.card {
  width: 35rem;
  height: 37rem;
  background-color: white;
  border-radius: 20px;
  box-shadow: -3px 5px 12px -4px rgba(0, 0, 0, 0.75);
  margin-bottom: 2rem;
}
.card img {
  width: 100%;
  height: 20rem;
  border-radius: 20px 20px 0 0;
}
.card .info {
  width: 100%;
  height: 10rem;
  padding: 1rem;
  text-align: left;
  line-height: 1.3;
}
.card .info h3 {
  font-size: 2.5rem;
  margin-bottom: 0.5rem;
}
.card .info h3:hover {
  cursor: pointer;
}
.card .info i {
  margin: 0.5rem 1rem;
  font-size: 2.5rem;
}
.card .info .info_more {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 5rem;
}
.card .info .info_more .etiqueta {
  margin-top: 1rem;
  width: 20rem;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
.card .info .info_more .etiqueta p {
  margin: 0 0.5rem;
  font-weight: bold;
}
.card .info .info_more .actions {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
}
.card .info .info_more .actions p {
  margin-right: 1rem;
}
.card .info .info_more .actions i {
  font-size: 1.5rem;
}
.card .info .info_more .actions .heart {
  fill: red;
  position: relative;
  width: 25px;
  animation: pulse 1s ease infinite;
}
.card .info .info_more .actions .heart:hover {
  cursor: pointer;
}

.btn {
  display: block;
  text-transform: uppercase;
  font-weight: bold;
  padding: 0.3rem 2rem;
  background-color: black;
  color: white;
  width: 11rem;
  padding: 0.8rem 0;
  text-align: center;
  margin: 2rem auto;
  font-size: 1.8rem;
}
.btn:hover {
  cursor: pointer;
}